.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.dataInput {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}

.dataInput input{
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5px;
  margin-left: 10px;
}

.dataInput button {
  margin-left: 10px;
  width: 70px;
  cursor: pointer;
}

.dataInput .upCan {
  display: flex;
  flex-direction: row;
  margin-left: 10px;
  width: 80px;
  cursor: pointer;

}

.dataList {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.dataList p {
  margin-left: 10px;
}

.dataList button {
  margin-left: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
